/* LogementDescription.css */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

.header h1 {
  font-size: 24px;
  color: #333;
}

.logement-details {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.logement-details img {
  width: 100%;
  border-radius: 8px;
}

.logement-details h2 {
  font-size: 20px;
  color: #333;
  margin: 0;
}

.logement-details p {
  color: #666;
  line-height: 1.6;
}

.button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  text-align: center;
  display: inline-block;
}

.button:hover {
  background-color: #45a049;
}