/* Bailleur.css */
body {
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #f6f9fc 0%, #e9ecef 100%);
  margin: 0;
  padding: 20px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.bailleur-form {
  margin-top: 50%;
  width: 100%;
  max-width: 500px;
  padding: 2rem;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.bailleur-form h2 {
  color: #2d3748;
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
}

.bailleur-form form > div {
  margin-bottom: 1.5rem;
}

.bailleur-form label {
  display: block;
  margin-bottom: 0.5rem;
  color: #4a5568;
  font-size: 0.95rem;
  font-weight: 500;
}

.bailleur-form input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.bailleur-form input:focus {
  border-color: #4299e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.1);
  outline: none;
}

.bailleur-form input:hover {
  border-color: #4299e1;
}

.profile-input {
  margin-bottom: 1.5rem;
}

.profile-input input[type="file"] {
  padding: 0.5rem;
  background: #f7fafc;
}

.error {
  background: #fff5f5;
  color: #e53e3e;
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  border: 1px solid #feb2b2;
  text-align: center;
}

button[type="submit"] {
  width: 100%;
  padding: 0.75rem;
  background: #4299e1;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

button[type="submit"]:hover {
  background: #3182ce;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(66, 153, 225, 0.2);
}

button[type="submit"]:active {
  transform: translateY(0);
}

@media (max-width: 480px) {
  .bailleur-form {
      padding: 1.5rem;
      margin: 1rem;
  }
  
  .bailleur-form h2 {
      font-size: 1.5rem;
  }
}